.link {
  text-decoration: none;
  font-weight: bold;
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.item {
display: inline-block;
margin-left: 30px;
margin-bottom: 30px;
flex-basis: calc(100% / 3 - 30px);
max-width: 270px;
border-radius: 5px;
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.item:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.title {
  font-size: 25px;
  color: #d8d8d8;
}

.titleWrap{
text-align: center;
padding: 20px;
}


